import * as fs from "fs";
import { context } from "./context";

type RawValue = {
  name: string;
  value: string;
  branch?: string;
  repository?: string;
};
export const getValues = () => {
  const path = `${context.workingDir}/ct/values.json`;

  if (!fs.existsSync(path)) {
    return {};
  }
  const values: RawValue[] = JSON.parse(fs.readFileSync(path, "utf8"));
  return values
    .filter(
      (v) =>
        (v.branch === context.branch || !v.branch) &&
        (v.repository === context.repository || !v.repository)
    )
    .reduce((p, v) => {
      p[v.name] = v.value;
      return p;
    }, {} as Record<string, string>);
};
